  <v-container fill-height fluid>
    <v-row no-gutters justify="center" align="center">
      <div class="d-flex flex-column">
        <div class="d-flex justify-space-between">
          <v-img
            lazy-src="https://picsum.photos/id/11/10/6"
            max-height="350"
            max-width="450"
            src="https://picsum.photos/id/11/500/300"
          ></v-img>
          <div class="d-flex flex-column justify-space-between align-start ml-5 py-5">
            <h1> : {{ userInfo.name }}</h1>
            <h1> : {{ userInfo.email }}</h1>
            <h1>SNS   : {{ userInfo.sns }}</h1>
            <div>
              <v-btn></v-btn>
              <v-btn></v-btn>
            </div>
          </div>
        </div>
        
        <h2 class="d-flex flex-column align-start mt-5">
           
        </h2>
        <hr>
        <v-virtual-scroll
          height="150"
          item-height="50"
          :items="solvedProblems"
        >
          <template v-slot="{ item }">
            <v-list-item :key="item.id">
              <v-list-item-content>
                <v-list-item-title>
                  <strong>{{ item.title }}. {{ item.content }} </strong>
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-divider></v-divider>
          </template>
        </v-virtual-scroll>
      </div>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios'

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  name: 'UserProfile',
  data: () => {
    return {
      userInfo: {
        name: '',
        email: '',
        sns: '',
      },
      solvedProblems: [
        {id: '1', title: '1', content: '1   .'},
        {id: '2', title: '2', content: '2   .'},
        {id: '3', title: '3', content: '3   .'},
        {id: '4', title: '4', content: '4   .'},
        {id: '5', title: '5', content: '5   .'},
      ]
    }
  },
  methods: {
    setToken() {
      const token = localStorage.getItem("jwt")
      if (token) {
        console.log("  ")
        return token;
      }
    }
    // 1.      :email axios get
    // 2.       setToken    axios post
    // 3.           
    // 4. 3.    
    // 5. 3.           
  },
  created() {
    // methods       
    const userKey = {
      'access-token': this.setToken()
    }

    // console.log(userKey);

    axios.post(`${SERVER_URL}/user/info`, userKey)
    .then(response => {
      console.log(response);
    })
    .catch(error => {
      console.log(error);
    })
  }
}
</script>

