<template>
  <div>
    <v-chip style="position: absolute; top: 0; left: 10px; z-index: 50;">
        
    </v-chip>
    <v-tabs v-model="tabs" centered class="mt-15">
      <!-- v-tabsdml v-model="tabs"    tabs  -->
      <!-- <v-tab v-for="n in 3" :key=n>   for  -->
      <v-tab> </v-tab>
      <v-tab> </v-tab>
      <v-tab></v-tab>

      <v-tabs-items v-model="tabs" class="mt-15">
        <!--     tabs   v-tab-item   -->
        <!-- ' '  () -->
        <v-tab-item>
          <v-container>
            <!--  (    -->
            <v-row no-gutters justify="center">
              <v-col cols="12" md="4">
                <v-textarea
                label="Your Answer"
                no-resize
                rows="5"
                :value="yourAnswer"
                readonly="true"
                outlined
                ></v-textarea>
              </v-col>

              <v-col cols="0" md="1"></v-col>

              <v-col cols="12" md="4">
                <v-textarea
                  label="Correct Answer"
                  no-resize
                  rows="5"
                  :value="correctAnswer"
                  readonly="true"
                  outlined
                ></v-textarea>
              </v-col>
            </v-row>
          </v-container>
        </v-tab-item>

        <!-- ' '   -->
        <v-tab-item>
          <v-contatiner>
            <v-row class="mt-1" no-gutters justify="center">
              <v-col cols="12" sm="8" md="4">
                <v-textarea
                  label="Your Answer"
                  no-resize
                  rows="20"
                  :value="yourCode"
                  readonly="true"
                  outlined
                ></v-textarea>
              </v-col>
              <v-col cols="0" sm="1"></v-col>
              <v-col cols="12" sm="8" md="4">
                <v-textarea
                  label="Correct Answer"
                  no-resize
                  rows="20"
                  :value="correctCode"
                  readonly="true"
                  outlined
                ></v-textarea>
              </v-col>
            </v-row>
          </v-contatiner>
        </v-tab-item>

        <!-- ''   -->
        <v-tab-item>
          <v-container>
            <v-row>
              <!--    -->
              <v-carousel v-model="model">
                <v-carousel-item
                  v-for="(color, i) in colors"
                  :key="color"
                >
                  <v-sheet
                    :color="color"
                    height="100%"
                    tile
                  >
                    <v-row
                      class="fill-height"
                      align="center"
                      justify="center"
                    >
                      <div class="display-3">
                         {{ i + 1 }}
                      </div>
                    </v-row>
                  </v-sheet>
                </v-carousel-item>
              </v-carousel>
            </v-row>
          </v-container>
        </v-tab-item>
      </v-tabs-items>
    </v-tabs>
  </div>
</template>

<script>
export default {
  name: 'SolveResult',
  data: () => {
    return {
      tabs: null,
      description: false,
      isCorrect: false,
      yourAnswer: '   \n  ',
      correctAnswer: '   ',
      yourCode: '   \n   ',
      correctCode: '   ',
      model: 0,
      colors: [
        'primary',
        'secondary',
        'yellow darken-2',
        'red',
        'orange',
      ],
    }
  },
  methods: {
    mouseOver: function() {
      this.description = !this.description;
    },
    mouseLeave: function() {
      this.description = !this.description;
    },
  }
}
</script>

