<template>
  <div id="WebMeeting">
    <Navbar />
    <div id="join" v-if="!session" class="underRoom">
      <div id="join-dialog" class="jumbotron vertical-center">
        <h1> !!!</h1>
        <div class="form-group">
          <p>
            <label></label>
            <input
              v-model="myUserName"
              class="form-control"
              type="text"
              required
            />
          </p>
          <p>
            <label></label>
            <input
              v-model="mySessionId"
              class="form-control"
              type="text"
              required
            />
          </p>
          <p class="text-center">
            <button class="btn btn-lg btn-success" @click="joinSession()">
              
            </button>
          </p>
        </div>
      </div>
    </div>

    <!-- ///////////////////   ///////////////// -->

    <div id="session" v-if="session" class="underRoom">
      <div id="session-header">
        <h1 id="session-title">{{ mySessionId }}</h1>
        <input
          class="btn btn-large btn-outline-secondary"
          type="button"
          id="buttonLeaveSession"
          @click="leaveSession"
          value=""
        />
      </div>

      <!-- //////////////// chat ///////////////////// -->

      <!-- <div id="main-video" class="col-md-6">
         <user-video :stream-manager="mainStreamManager" />
      </div> -->
      <div id="roomContents">
        <div id="video-container">
          <div class="video_test">
            <user-video
              :stream-manager="publisher"
              @click.native="updateMainVideoStreamManager(publisher)"
            />
            <user-video
              v-for="sub in subscribers"
              :key="sub.stream.connection.connectionId"
              :stream-manager="sub"
              @click.native="updateMainVideoStreamManager(sub)"
            />
          </div>
        </div>
        <div id="chatContainer">
          <span
            style="
              height: 40px;
              float: left;
              width: 100%;
              border-bottom: 1px solid black;
              text-align: center;
              font-size: 30px;
            "
            >CHAT</span
          >

          <div class="message-wrap">
            <p
              class="chatlist"
              v-for="(msg, index) in messageList"
              :key="index"
              v-text="msg.message"
            ></p>
          </div>

          <div id="messageInput">
            <!-- <select v-model="to" class="chatselect">
            <option
              v-for="sub in subscribers"
              :key="sub.stream.connection.connectionId"
              v-text="sub.stream.connection.connectionId"
              v-bind="sub.stream"
            ></option>
          </select> -->
            <input
              placeholder=" ..!"
              id="chatInput"
              v-model="message"
            />
            <button id="sendButton" @click="sendMessage">SEND</button>
          </div>
        </div>

        <!-- //////////////// chat ///////////////////// -->
      </div>
    </div>
  </div>
</template>


<script>
import axios from "axios";
import { OpenVidu } from "openvidu-browser";
import UserVideo from "../components/UserVideo";

axios.defaults.headers.post["Content-Type"] = "application/json";
const SERVER_URL = process.env.VUE_APP_SERVER_URL;
const OPENVIDU_SERVER_URL = "https://i4a203.p.ssafy.io";
const OPENVIDU_SERVER_SECRET = "codackji";

export default {
  name: "WebMeeting",
  components: {
    UserVideo,
  },
  data() {
    return {
      OV: undefined,
      session: undefined,
      mainStreamManager: undefined,
      publisher: undefined,
      subscribers: [],
      token: " ",
      mySessionId: "Room",
      myUserName: "player" + Math.floor(Math.random() * 100),

      // chat
      messageList: [],
      message: "",
      to: "",
    };
  },

  created() {
    this.joinSession();
  },

  methods: {
    // chat

    sendMessage() {
      console.log(this.message);
      console.log(this.to);
      // if (this.props.user && this.message) {
      if (this.message) {
        let message = this.message.replace(/ +(?= )/g, "");
        if (message !== "" && message !== " ") {
          const data = {
            message: this.myUserName + " : " + message,
            nickname: this.myUserName,
            streamId: this.publisher.stream.streamId,
          };
          this.publisher.session.signal({
            data: JSON.stringify(data),
            to: [this.to],
            type: "chat",
          });
        }
      }
      this.message = "";
    },

    joinSession() {
      // --- Get an OpenVidu object ---
      this.OV = new OpenVidu();

      // --- Init a session ---
      this.session = this.OV.initSession();

      // --- Specify the actions when events take place in the session ---

      // On every new Stream received...
      this.session.on("streamCreated", ({ stream }) => {
        const subscriber = this.session.subscribe(stream);
        this.subscribers.push(subscriber);
      });

      // On every Stream destroyed...
      this.session.on("streamDestroyed", ({ stream }) => {
        const index = this.subscribers.indexOf(stream.streamManager, 0);
        if (index >= 0) {
          this.subscribers.splice(index, 1);
        }
      });

      // --- Connect to the session with a valid user token ---

      // 'getToken' method is simulating what your server-side should do.
      // 'token' parameter should be retrieved and returned by your own backend
      // this.getToken(this.mySessionId).then((token) => {
      axios
        .post(`${SERVER_URL}/sessions/maketoken`)
        .then((response) => {
          console.log(response);
          this.token = response.data.token;

          this.session
            .connect(this.token, { clientData: this.myUserName })
            .then(() => {
              // --- Get your own camera stream with the desired properties ---

              let publisher = this.OV.initPublisher(undefined, {
                audioSource: undefined, // The source of audio. If undefined default microphone
                videoSource: undefined, // The source of video. If undefined default webcam
                publishAudio: true, // Whether you want to start publishing with your audio unmuted or not
                publishVideo: true, // Whether you want to start publishing with your video enabled or not
                resolution: "640x480", // The resolution of your video
                frameRate: 30, // The frame rate of your video
                insertMode: "APPEND", // How the video is inserted in the target element 'video-container'
                mirror: false, // Whether to mirror your local video or not
              });

              this.mainStreamManager = publisher;
              this.publisher = publisher;

              // --- Publish your stream ---
              this.messageList = [];
              this.session.publish(this.publisher);
              console.log(" ");
              console.log(this.subscribers);
              //chat
              this.publisher.session.on("signal:chat", (event) => {
                const data = JSON.parse(event.data);
                this.messageList.push({
                  // connectionId: event.from.connectionId,
                  nickname: data.nickname,
                  message: data.message,
                });
              });
              //chat
            })
            .catch((error) => {
              console.log(
                "There was an error connecting to the session:",
                error.code,
                error.message,
              );
            });
        });
      window.addEventListener("beforeunload", this.leaveSession);
      console.log(this.stream.connection.connectionId);
    },

    leaveSession() {
      // --- Leave the session by calling 'disconnect' method over the Session object ---
      if (this.session) this.session.disconnect();

      this.session = undefined;
      this.mainStreamManager = undefined;
      this.publisher = undefined;
      this.subscribers = [];
      this.OV = undefined;

      window.removeEventListener("beforeunload", this.leaveSession);
      this.$router.push("main");
    },

    updateMainVideoStreamManager(stream) {
      if (this.mainStreamManager === stream) return;
      this.mainStreamManager = stream;
    },

    /**
     * --------------------------
     * SERVER-SIDE RESPONSIBILITY
     * --------------------------
     * These methods retrieve the mandatory user token from OpenVidu Server.
     * This behavior MUST BE IN YOUR SERVER-SIDE IN PRODUCTION (by using
     * the API REST, openvidu-java-client or openvidu-node-client):
     *   1) Initialize a Session in OpenVidu Server   (POST /openvidu/api/sessions)
     *   2) Create a Connection in OpenVidu Server (POST /openvidu/api/sessions/<SESSION_ID>/connection)
     *   3) The Connection.token must be consumed in Session.connect() method
     */

    getToken(mySessionId) {
      return this.createSession(mySessionId).then((sessionId) =>
        this.createToken(sessionId),
      );
    },

    // See https://docs.openvidu.io/en/stable/reference-docs/REST-API/#post-openviduapisessions
    createSession(sessionId) {
      return new Promise((resolve, reject) => {
        axios
          .post(
            `${OPENVIDU_SERVER_URL}/openvidu/api/sessions`,
            JSON.stringify({
              customSessionId: sessionId,
            }),
            {
              auth: {
                username: "OPENVIDUAPP",
                password: OPENVIDU_SERVER_SECRET,
              },
            },
          )
          .then((response) => response.data)
          .then((data) => resolve(data.id))
          .catch((error) => {
            if (error.response.status === 409) {
              resolve(sessionId);
            } else {
              console.warn(
                `No connection to OpenVidu Server. This may be a certificate error at ${OPENVIDU_SERVER_URL}`,
              );
              if (
                window.confirm(
                  `No connection to OpenVidu Server. This may be a certificate error at ${OPENVIDU_SERVER_URL}\n\nClick OK to navigate and accept it. If no certificate warning is shown, then check that your OpenVidu Server is up and running at "${OPENVIDU_SERVER_URL}"`,
                )
              ) {
                location.assign(`${OPENVIDU_SERVER_URL}/accept-certificate`);
              }
              reject(error.response);
            }
          });
      });
    },

    // See https://docs.openvidu.io/en/stable/reference-docs/REST-API/#post-openviduapisessionsltsession_idgtconnection
    createToken(sessionId) {
      return new Promise((resolve, reject) => {
        axios
          .post(
            `${OPENVIDU_SERVER_URL}/openvidu/api/sessions/${sessionId}/connection`,
            {},
            {
              auth: {
                username: "OPENVIDUAPP",
                password: OPENVIDU_SERVER_SECRET,
              },
            },
          )
          .then((response) => response.data)
          .then((data) => resolve(data.token))
          .catch((error) => reject(error.response));
      });
    },
  },
};
</script>

<style>
#video-container {
  width: 80%;
}

#chatContainer {
  height: 100%;
  /* border: 1px solid black; */
  width: 20%;
}

.message-wrap {
  height: 75vh;
  box-shadow: 2px 2px 3px 3px grey;
  width: 100%;
  overflow: scroll;
  overflow-x: hidden;
}

.chatlist {
  width: 100%;
  height: 5%;
  float: left;
  margin: 8px 0 0 8px;
  font-size: 16px;
}

.video_test {
  width: 100% !important;
  height: 100% !important;
  display: flex;
  flex-wrap: wrap;
  padding: 5%;
}
.video_test > div {
  overflow: hidden;
  height: auto;
  width: 45%;
}
#messageInput {
  float: left;
  width: 100%;
  height: 40px;
  margin: 20px 0 0 0;
}

.chatselect {
  float: left;
  width: 100%;
  height: 20px;
}

#chatInput {
  float: left;
  margin: 10px 0 0 0;
  width: 78%;
  height: 20px;
}
#sendButton {
  float: left;
  width: 18%;
  margin: 10px 0 0 5px;
  height: 20px;
}
.underRoom {
  padding-top: 61px;
}
#roomContents {
  display: flex;
}
#session-header {
  display: flex;
  justify-content: space-between;
}
#buttonLeaveSession {
  margin-right: 60px;
  height: 50%;
}
#session-title {
  margin-left: 30%;
}
video {
  border: 1px solid rgb(184, 180, 180, 0.8);
}
</style>